    position: relative;
    resize: both;
    overflow: auto;

    box-sizing: border-box;

    min-width: 2rem;
    width: 20rem;
    max-width: 100%;

    min-height: 2rem;
    height: auto;
    max-height: 20rem;

    padding: 1rem 1rem 3rem 1rem;
    border: 0.125rem dashed rgb(var(--contrast-500));

    border-radius: 0.5rem;

    &::after {
        content: 'Resize me â¤µ';
        font-size: 0.75rem;
        position: absolute;
